<template>
  <div class="line-chart" ref="lineChart"></div>
</template>

<script>
//
import * as echarts from "echarts";
export default {
  name: "LineChart",
  mounted() {
    //echarts
    //refline-Chart as requireinit
    // let echarts = require('echarts')
    let lineChart = echarts.init(this.$refs.lineChart);
    //
    lineChart.setOption({
      xAxis: {
        //xy
        show: false,
        //
        type: "category",
      },
      yAxis: {
        show: false,
      },
      //
      series: [
        {
          type: "line",
          data: [1, 5,3,5,7, 3, 5,],
          //0
          itemStyle: {
            opacity: 0,
          },
          //
          lineStyle:{
              color:"purple"
          },
          // 
          areaStyle: {
            color: {
              type: "linear",
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [
                {
                  offset: 0,
                  color: "purple", // 0% 
                },
                {
                  offset: 1,
                  color: "#fff", // 100% 
                },
              ],
              global: false, //  false
            },
          },
        },
      ],
      //
      grid: {
        left: 0,
        top: 0,
        right: 0,
        bottom: 0,
      },
    });
  },
};
</script>

<style scoped>
.line-chart {
  width: 100%;
  height: 100%;
}
</style>
