<template>
  <div class="bar-chart" ref="barChart"></div>
</template>

<script>
//
import * as echarts from "echarts";
export default {
  name: "BarChart",
  mounted() {
    //echarts
    //refline-Chart as requireinit
    // let echarts = require('echarts')
    let barChart = echarts.init(this.$refs.barChart);
    //
    barChart.setOption({
      xAxis: {
        //xy
        show: false,
        min:0,
        max:100
      },
      yAxis: {
        show: false,
        //y
        type:'category',
        
      },
      //
      series: [
        {
          type: 'bar',
          data: [79],
          color:'#33ab3d',
          // 
          barWidth:10,
          //
          showBackground:true,
          // backgroundStyle:'#eee' 
          //
          label:{
            show:true,
            //
            formatter:'|',
            //
            position:'right'
          }
        },
      ],
      //
      grid: {
        left: 0,
        top: 0,
        right: 0,
        bottom: 0,
      },
      //
      tooltip:{

      }
    });
  },
};
</script>

<style scoped>
.bar-chart {
  width: 100%;
  height: 100%;
}
</style>
