<template>
  <div class="bar-chart" ref="barChart"></div>
</template>

<script>
//
import * as echarts from "echarts";
export default {
  name: "BarChart",
  mounted() {
    //echarts
    //refline-Chart as requireinit
    // let echarts = require('echarts')
    let barChart = echarts.init(this.$refs.barChart);
    //
    barChart.setOption({
      xAxis: {
        //xy
        show: false,
        type:'category'
      },
      yAxis: {
        show: false,
      },
      //
      series: [
        {
          type: 'bar',
          data: [1,3,4,2,5,6,2,4,3],
          color:'cyan'
        },
      ],
      //
      grid: {
        left: 0,
        top: 0,
        right: 0,
        bottom: 0,
      },
      //
      tooltip:{

      }
    });
  },
};
</script>

<style scoped>
.bar-chart {
  width: 100%;
  height: 100%;
}
</style>
