<template>
  <div>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="">
        <el-input placeholder="" v-model="keyword"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small" icon="el-icon-search"
          @click="getBorrow(keyword)"></el-button
        >
        <el-button type="primary" size="small" @click="getBorrow()"></el-button>
      </el-form-item>
    </el-form>
    <!-- 
     -->
    <!-- <el-button type="primary" size="small " icon="el-icon-plus"></el-button> -->
    <el-table :data="list" style="width: 100%" border>
      <el-table-column type="index" label="" width="80px">
      </el-table-column>
      <el-table-column prop="bookName" label="" width="width">
      </el-table-column>
      <el-table-column prop="price" label="" width="width">
      </el-table-column>
      <el-table-column prop="borrowDate" label="" width="width">
      </el-table-column>
      <el-table-column prop="backDate" label="" width="width">
      </el-table-column>
      <el-table-column prop="memberName" label="" width="width">
      </el-table-column>
      <el-table-column prop="state_name" label="" width="100px">
        <template slot-scope="{ row }">
          <el-tag type="danger">{{ row.states_name }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="" label="" width="180px">
        <!-- 
          tablerow table:data row $index 
          table column   template  
          $indexrow0
         -->
        <!-- <template slot-scope="{ row, $index }"> -->
        <template slot-scope="{ row }">
          <el-button
            type="warning"
            icon="el-icon-edit"
            size="mini"
            :disabled="row.states_id == 3 ? false : true"
            @click="renewal"
            ></el-button
          >
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="deleteRecord(row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
    name:'Borrow',
    data(){
      return {
        list:[],
        keyword:''
      }
    },
    methods:{
      renewal(){
        this.$confirm('','',{
          confirmButtonText:'',
          cancelButtonText:'',
          type:'warning'
        }).then(
          ()=>this.$message.success('')
        ).catch( ()=>this.$message.info(''))
      },
      deleteRecord(row){
          this.$confirm('','',{
            confirmButtonText:'',
            cancelButtonText:'',
            type:'warning'
          }).then(
            async ()=>{
            const id = row.id
            let result = await this.$API.book.reqdeleteRecord({id})
            if(result.code == 200){
              this.$message.success('')
              this.getBorrow()
            }else {
              this.$message.error('')
            }
           
      })
      },
      async getBorrow(keyword){
        const name = keyword
        let result = await this.$API.book.reqqueryRecord({name});
        this.list = result.data.rows;
        this.keyword = ''
      }
    },

     mounted(){
      this.getBorrow();
    }
}
</script>

