<template>
  <div>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="" >
        <el-input placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small" icon="el-icon-search"
         ></el-button
        >
        <el-button type="primary" size="small" ></el-button>
      </el-form-item>
    </el-form>
    <!-- 
      -->
    <!-- <el-button
      type="primary"
      size="small "
      icon="el-icon-plus"
 
      ></el-button
    > -->
    <el-table :data="list" style="width: 100%" border >
      <el-table-column type="index" label="" width="width">
      </el-table-column>
      <el-table-column prop="name" label="" width="width">
      </el-table-column>
      <el-table-column prop="price" label="" width="width">
      </el-table-column>
      <el-table-column prop="publish" label="" width="width">
      </el-table-column>
      <el-table-column prop="borrowDate" label="" width="width">
      </el-table-column>
      <el-table-column prop="backDate" label="" width="width">
      </el-table-column>

      <el-table-column prop="states_name" label="" width="width">
        <template slot-scope="{row}">
            <!-- <el-tag type="success"></el-tag> -->
            <el-tag type="warning">{{row.states_name}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="" label="" width="180px">
        <!-- 
          tablerow table:data row $index 
          table column   template  
          $indexrow0
         -->
        <!-- <template slot-scope="{ row, $index }"> -->
          <template slot-scope="{row}"> 
          <el-button
            type="warning"
            icon="el-icon-edit"
            size="mini"
           :disabled="row.states_id == 3?false:true"
            @click="renewal(row)"></el-button
          >
          <el-button
            type="info"
            icon="el-icon-delete"
            size="mini"
             :disabled="row.states_id == 3?false:true"
             @click="returnBook(row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
    name:'Record',
    data(){
      return {
        list:[]
      }
    },
    methods:{
      async getqueryReaderRecord(){
        let result = await this.$API.book.reqqueryReaderRecord({"id":1});
        this.list=result.data.rows
      },
      async renewal(row){
        
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'}).then(async ()=>{
            const id = row.id
            let result = await this.$API.book.requpdateStates({id})
            if(result.code == 200){
              this.$message.success('')
              this.getqueryReaderRecord();    
            }else {
              this.$message.error('')
            }
  
          }).catch(()=>{
            this.$message.info('')
          })

      },
      returnBook(row){
         this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'}).then(async ()=>{
            const id = row.id
            let result = await this.$API.book.reqreturnBook({id})
            if(result.code == 200){
              this.$message.success('')
              this.getqueryReaderRecord();    
            }else {
              this.$message.error('')
            }
  
          }).catch(()=>{
            this.$message.info('')
          })
      }
    },
    async mounted(){
     this.getqueryReaderRecord();
    }
}
</script>

