<template>
  <div>
    <!-- -->
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="">
        <el-input
          placeholder=""
          v-model="searchbook"
          @keyup.native.enter="searchBook"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          size="small"
          icon="el-icon-search"
          @click="searchBook"
          ></el-button
        >
        <el-button type="primary" size="small" @click="getAllBook"
          ></el-button
        >
      </el-form-item>
    </el-form>
    
    <el-button
      type="primary"
      size="small "
      icon="el-icon-plus"
      @click="showDialog"
      v-show="token == 'admin' ? true : false"
      ></el-button
    >
    <el-table style="width: 100%" border :data="list">
      <el-table-column type="index" label="" width="80px">
      </el-table-column>
      <el-table-column prop="typeName" label="" width="80px">
      </el-table-column>
      <el-table-column prop="name" label="" width="width">
      </el-table-column>
      <el-table-column prop="price" label="" width="80px">
      </el-table-column>
      <el-table-column prop="publish" label="" width="width">
      </el-table-column>
      <el-table-column prop="author" label="" width="width">
      </el-table-column>
      <el-table-column prop="stock" label="" width="80px">
      </el-table-column>
      <el-table-column prop="" label="" width="240px">
        <!-- <template slot-scope="{ row, $index }"> -->
        <template slot-scope="{ row }">
          <el-button
            v-if="token == 'admin' ? true : false"
            title=""
            type="warning"
            icon="el-icon-edit"
            size="mini"
            @click="updataBook(row)"
          ></el-button>
          <el-button
            v-if="token == 'admin' ? true : false"
            title=""
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="delBook(row)"
          ></el-button>
          <el-button
            v-if="token == 'admin' ? false : true"
            title=""
            type="primary"
            icon="el-icon-star-off"
            size="mini"
            @click="borbook(row)"
          ></el-button>
          <el-button
            type="info"
            icon="el-icon-info"
            size="mini"
            title=""
            @click="selectBookById(row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 
     -->
    <el-pagination
      style="margin-top: 20px; text-align: center"
      :current-page="pageNo"
      :total="total"
      :page-size="pageSize"
      :page-sizes="[3, 5, 10]"
      :pager-count="7"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
      layout="prev, pager, next, jumper,sizes,total"
    >
    </el-pagination>
    <!--  
    -->
    <el-dialog
      :title="bookInfo.id ? '' : ''"
      :visible.sync="dialogFormVisible"
    >
      <el-form style="width: 60%" :model="bookInfo">
        <el-form-item label="" label-width="100px">
          <el-select v-model="bookInfo.typeId" placeholder="">
            <el-option
              v-for="item in typeInfo"
              :key="item.typeId"
              :label="item.name"
              :value="item.typeId"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input
            v-model="bookInfo.name"
            autocomplete="off"
            placeholder=""
            clearable
            :disabled="bookInfo.id ? true : false"
          ></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input
            v-model="bookInfo.price"
            autocomplete="off"
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input
            v-model="bookInfo.publish"
            autocomplete="off"
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input
            v-model="bookInfo.author"
            autocomplete="off"
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input
            v-model="bookInfo.stock"
            autocomplete="off"
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="addBookOrUpdata"> </el-button>
      </div>
    </el-dialog>

    <!-- 
         -->
    <el-drawer title="" :visible.sync="show" direction="rtl">
      <el-row :gutter="10">
        <el-col :span="6"></el-col>
        <el-col :span="15">{{bookInfo.name}}</el-col>
        <el-col :span="6"></el-col>
        <el-col :span="15">{{bookInfo.typeName}}</el-col>
        <el-col :span="6"></el-col>
        <el-col :span="15"></el-col>
        <el-col :span="6"></el-col>
        <el-col :span="15">{{bookInfo.publish}}</el-col>
        <el-col :span="6"></el-col>
        <el-col :span="15">{{bookInfo.author}}</el-col>
        <el-col :span="6"></el-col>
        <el-col :span="15"></el-col>
        <el-col :span="20">
          <!--  -->
          <template>
            <div class="block">
              <el-carousel height="150px">
                <el-carousel-item v-for="item in 4" :key="item">
                  <!-- <h3 class="small">{{ item }}</h3> -->
                  <img src="@/assets/login.png" class="book-img" />
                </el-carousel-item>
              </el-carousel>
            </div>
          </template>
        </el-col>
      </el-row>
    </el-drawer>
  </div>
</template>

<script>
import { reqAddBookOrUpdata, reqdelBook } from "@/api/book/book";
import { mapState } from "vuex";
export default {
  data() {
    return {
      pageNo: 1,
      pageSize: 3,
      total: 0,
      dialogFormVisible: false,
      bookInfo: {
        typeId: "",
        name: "",
        price: "",
        publish: "",
        author: "",
        stock: "",
      },
      typeInfo: [
        {
          typeId: 1,
          name: "",
        },
        {
          typeId: 2,
          name: "",
        },
        {
          typeId: 3,
          name: "",
        },
      ],
      searchbook: "",
      list: [],
      show: false,
      token: "",
    };
  },
  methods: {
    async getBookList() {
      const { pageNo, pageSize } = this;
      let result = {};
      if (this.searchbook != "") {
        const name = this.searchbook;
        result = await this.$API.book.reqqueryBookList({
          name,
          pageNo,
          pageSize,
        });
      } else {
        result = await this.$API.book.reqqueryBookList({ pageNo, pageSize });
      }
      this.list = result.data.rows;
      this.total = result.data.total;
    },
    handleCurrentChange(pageNo) {
      this.pageNo = pageNo;
      this.getBookList();
    },
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.getBookList();
    },
    showDialog() {
      this.dialogFormVisible = true;
      //
      this.bookInfo = {
        typeId: "",
        name: "",
        price: "",
        publish: "",
        author: "",
        stock: "",
      };
    },
    async addBookOrUpdata() {
      if (
        this.bookInfo.typeId != "" &&
        this.bookInfo.name != '' &&
        this.bookInfo.price != '' &&
        this.bookInfo.publish != '' &&
        this.bookInfo.author != '' &&
        this.bookInfo.stock != ''
      ) {
        let result = await this.$API.book.reqAddBookOrUpdata(this.bookInfo);
        // this.$message.success(result.code==200?'':'');
        if (result.code == 200) {
          this.$message({
            type: "success",
            message: this.bookInfo.id ? "" : "",
          });

          this.getBookList(this.bookInfo.id ? this.pageNo : 1);
          this.dialogFormVisible = false;
        }
      } else {
        this.$message({
          message: "",
          type: "error",
        });
      }
    },
    updataBook(row) {
      this.dialogFormVisible = true;
      //
      this.bookInfo = { ...row };
      console.log(this.pageNo);
    },
    delBook(row) {
      //
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          const id = row.id;
          let result = await reqdelBook({ id });
          if (result.code == 200) {
            this.$message.success("");
            this.getBookList(this.bookInfo.id ? this.pageNo : 1);
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    async searchBook() {
      const name = this.searchbook;
      const pageNo = 1;
      const pageSize = 3;
      if (name != "") {
        let result = await this.$API.book.reqqueryBookList({
          name,
          pageNo,
          pageSize,
        });
        if (result.data.total > 0) {
          this.list = [];
          this.list = result.data.rows;
          this.total = result.data.total;
          console.log(this.list);
        } else {
          this.$message.warning("");
        }
      } else {
        this.$message.error("");
      }
    },
    getAllBook() {
      this.searchbook = "";
      this.pageNo = 1;
      this.pageSize = 3;
      this.getBookList();
    },
    // 
    selectBookById(row) {
      this.show = true;
      this.bookInfo = row
    },
    // 
    borbook(row) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(async () => {
        console.log(row);
        let date = new Date();
        let seperator1 = "-";
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        let str2Date = strDate + 7;
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let book_id = row.id;
        let borrowDate = year + seperator1 + month + seperator1 + strDate;
        let backDate = year + seperator1 + month + seperator1 + str2Date;
        let member_id = 1;
        let result = await this.$API.book.reqborBook({
          book_id,
          borrowDate,
          backDate,
          member_id,
        })
        if(result.code == 200){
          this.$message.success(',')
        }else {
          this.$message.error(',')
        }
      });
    },
  },
  mounted() {
    this.getBookList();
    this.token = localStorage.getItem("TOKEN");
  },
};
</script>
 
<style scoped>
.el-row .el-col-6 {
  text-align: right;
  font-size: 16px;
}
.el-col {
  margin: 20px 10px;
}
.block {
  padding-left: 80px;
}
.book-img {
  width: 100%;
  height: 100%;
}
/* button */
>>> .el-carousel__button {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
</style>